import React from 'react';

import mainCarImg from '../assets/images/mainCarImg.png';
import mainCarTextImg from '../assets/images/mainCarTextImg.png';

function MainCar() {
  return (
    <div style={styles.mainCardiv}>
      <div style={styles.textContainer}>
        <img
          src={mainCarTextImg}
          style={styles.mainCarTextImg}
          alt='mainCarTextImg'
        />
      </div>

      {/* mainEVImg   */}
      <img
        src={mainCarImg}
        style={styles.mainCarImg}
        alt='mainCarImg'
      />
    </div>
  );
}

const styles = {
  textContainer: {
    display: 'flex', // Flexbox 
    justifyContent: 'center', //   
    alignItems: 'center', //   
    width: '100%',
    height: 'auto',
    backgroundColor: 'black',
    paddingTop: '20px',
    paddingBottom: '20px',
  },
  mainCarTextImg: {
    width: '30%', //    
    transform: 'translateX(-180px)', //    (  )
  },
  mainCarImg: {
    width: '100%',
  },
  mainCardiv: {
    display: 'flex',
    flexDirection: 'column',
  },
};

export default MainCar;
