import React, { useState } from 'react';
import axios from 'axios';

import MainCar from './MainCar';
import { Searchbar } from './Searchbar';
import CarSwiper from './CarSwiper';

import hyundailogo from '../assets/images/hyundailogo.png';
import kialogo from '../assets/images/kialogo.png';
import audilogo from '../assets/images/audilogo.png';
import bmwlogo from '../assets/images/bmwlogo.png';
import benzlogo from '../assets/images/benzlogo.png';

import CarImageDB from './CarImageDB.js'; //   
import SearchResult from './SearchResult.js';

function Main() {
  const [searchValue, setSearchValue] = useState(''); //    
  const [searchResults, setSearchResults] = useState([]);
  const [loading, setLoading] = useState(false); //     
  const [errorMessage, setErrorMessage] = useState(''); //    
  const [showAllBrands, setShowAllBrands] = useState(true); //      

  const hyundaiVehicles = CarImageDB.filter((vehicle) => vehicle.brand === 'Hyundai');
  const kiaVehicles = CarImageDB.filter((vehicle) => vehicle.brand === 'Kia');
  const audiVehicles = CarImageDB.filter((vehicle) => vehicle.brand === 'Audi');
  const bmwVehicles = CarImageDB.filter((vehicle) => vehicle.brand === 'BMW');
  const benzVehicles = CarImageDB.filter((vehicle) => vehicle.brand === 'Mercedes-Benz');

  // API   
  const fetchCarsFromAPI = async (carName) => {
    try {
      const response = await axios.get('https://port-0-java-springboot-m0uuimo09c0b9ce4.sel4.cloudtype.app/api/cars', {
        params: {
          carName, //  
        },
      });
      return response.data; //    
    } catch (error) {
      throw new Error('API  ');
    }
  };

  const handleSearch = async () => {
    if (!searchValue) {
      setErrorMessage(' .');
      return;
    }

    setLoading(true);
    setErrorMessage('');

    try {
      const vehicles = await fetchCarsFromAPI(searchValue); // API  
      const filteredVehicles = vehicles.filter((vehicle) => vehicle.name.toLowerCase().includes(searchValue.toLowerCase()));

      if (!filteredVehicles || filteredVehicles.length === 0) {
        setErrorMessage('  .');
        setShowAllBrands(true); //        
      } else {
        //    
        const vehiclesWithImages = filteredVehicles.map((vehicle) => {
          const matchingVehicle = CarImageDB.find(
            (v) => v.name === vehicle.name //     
          );
          return {
            ...vehicle,
            image: matchingVehicle ? matchingVehicle.image : null, //   
            logo: matchingVehicle ? matchingVehicle.logo : null, //  
          };
        });
        setSearchResults(vehiclesWithImages); //     
        setShowAllBrands(false); //       
      }
    } catch (error) {
      setErrorMessage(' .  .');
    } finally {
      setLoading(false);
    }
  };
  return (
    <div>
      <MainCar />
      <Searchbar
        value={searchValue}
        onChange={(e) => setSearchValue(e.target.value)}
        onSearch={handleSearch}
      />
      <div
        id='car-swiper-section'
        style={styles.swiperContainer}>
        {loading ? (
          <div> ...</div>
        ) : errorMessage ? (
          <div className='error'>{errorMessage}</div>
        ) : showAllBrands ? (
          <>
            <div style={styles.allmodel}>  </div>

            <CarSwiper
              logo={hyundailogo}
              brand='Hyundai'
              images={hyundaiVehicles}
            />
            <CarSwiper
              logo={kialogo}
              brand='Kia'
              images={kiaVehicles}
            />
            <CarSwiper
              logo={benzlogo}
              brand='Benz'
              images={benzVehicles}
            />
            <CarSwiper
              logo={bmwlogo}
              brand='BMW'
              images={bmwVehicles}
            />
            <CarSwiper
              logo={audilogo}
              brand='Audi'
              images={audiVehicles}
            />
          </>
        ) : (
          <SearchResult
            results={searchResults}
            searchTerm={searchValue}
          />
        )}
      </div>
    </div>
  );
}

//   
const styles = {
  swiperContainer: {
    marginLeft: '80px',
    marginRight: '80px',
    marginBottom: '100px',
  },
  allmodel: {
    marginTop: '120px',
    marginBottom: '120px',
    marginLeft: '50px',
    fontSize: '60px',
    letterSpacing: '7.8px',
    fontWeight: 400,
    fontFamily: 'JalnanGothic',
  },
};

export default Main;
